<script>
	export default {

		data() {
			return {
				name: '李韵',
				xingbie: '男',
				shengri: '例如：1990-08-20',
				shenfenhao: '',
				shuxiang: '',
				minzu: '',
				xiangzuo: '',
				jiating_txt: '单身贵族/为人父母',
				guxiang: '',
				yuyan: '',
				jiating: ['单身贵族', '为人父母'],
			}
		},
		created() {

		},
		onLoad(option) {


		},
		methods: {
			navigateBack() {
				uni.navigateBack()
			},
			shengri_change(data) {

				this.shengri = data.detail.value
			},
			jiating_change(data) {
				this.jiating_txt = this.jiating[data.detail.value]
			},
			save() {

				console.log(this.name, this.xingbie, this.shengri, this.shenfenhao, this.shuxiang, this.minzu, this.xiangzuo, this.jiating_txt,
					this.guxiang, this.yuyan)
			}

		}
	}
</script>

<style>
	.head {
		border-bottom: 2rpx solid #E8E8E8;
	}

	.shiming_list {
		height: 100rpx;
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-bottom: 2rpx solid #E8E8E8;
		padding-left: 30rpx;
		padding-right: 30rpx;
		position: relative;
	}

	.shiming_list input {
		width: 500rpx;

	}

	.shiming_list_left {
		width: 170rpx;
	}

	.jianjie {
		padding: 0 30rpx;
		line-height: 100rpx;
	}

	.jianjie_txt {
		font-size: 28rpx;
		width: 100%;
	}
	.zhuanchang_xuan {
		width: 500rpx;
		height: 100rpx;
		display: flex;
		align-items: center;
	}

	.zhuanchang_xuan image {
		width: 13rpx;
		height: 23rpx;
		position: absolute;
		right: 30rpx;

	}
</style>
